.menu {
  margin: 10px;
  background-color: #fff;

  &__header {
    padding: 16px;
  }

  &__tree-select {
    width: 100%;
  }
}

.log {
  :global(.ant-card-body) {
    padding: 0;
  }

  &__content {
    height: 500px;
    padding: 16px 24px;
    overflow: auto;
    white-space: pre-line;
  }

  &__alert {
    &:global(.ant-alert-no-icon) {
      padding: 4px;
      text-align: center;
    }
  }
}
